<template>
  <div class="params">
    <div id="leixin" v-show="purchaseprice">
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline" ref="filterFormRef">
          <el-form-item label="库存组织" size="mini">
            <el-select clearable v-model="params.company_id" placeholder="请选择" @change="companyChange" filterable @focus="companyListInvok">
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type == 1 ? 'optionStyle' : ''">{{
									item.company_name
									}}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="业务日期" size="mini">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="单据号" size="mini">
            <el-input v-model="params.instorage_sn" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="供应商(厂家)" size="mini">
            <el-select clearable v-model="params.supplier_id" maxlength="100" @change="supplierchange" :filter-method="supplierListInvok" @focus="supplierListInvok" filterable placeholder="请选择">
              <el-option v-for="item in supplierList" :label="item.supplier_name" :value="item.supplier_id" :key="item.supplier_id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="仓库名称" size="mini">
            <el-select clearable v-model="params.warehouse_id" placeholder="请选择" filterable @focus="warehouseInvok" @change="ckChange">
              <el-option v-for="item in warehouseList" :key="item.warehouse_id" :label="item.warehouse_name" :value="item.warehouse_id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="入库类型" size="mini">
            <el-select clearable v-model="params.type" placeholder="请选择">
              <el-option label="其他入库" :value="2"></el-option>
              <el-option label="盘盈入库" :value="4"></el-option>
              <el-option label="调拨入库" :value="5"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="部门" size="mini">
            <el-select clearable v-model="params.department_id" placeholder="请选择" @change="departmentChange" filterable @focus="DepartmentListInvok(params)">
              <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="采购员" size="mini">
            <el-select clearable v-model="params.buyer_id" @change="businessIdChange" @focus="EmployeeListInvok">
              <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="关联单号" size="mini">
            <el-input v-model="params.order_sn" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="审核状态" size="mini">
            <el-select clearable v-model="params.auditor_status">
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="未审核" :value="1">未审核</el-option>
              <el-option label="已审核" :value="2">已审核</el-option>
              <el-option label="作废" :value="3">作废</el-option>
            </el-select>
          </el-form-item>
          <el-form-item size="mini">
            <el-checkbox v-model="params.is_zero" false-label="0" true-label="1" name="type" style="margin-left:110px;">
              打印次数为0</el-checkbox>
          </el-form-item>
          <el-form-item size="mini" class="search-style">
            <el-button @click="onSearch(1)" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addF" class="addBtn" size="mini" v-if="authority.b_add == 1">新增</el-button>
        <el-button plain icon="el-icon-delete" @click="deletes" size="mini" v-if="authority.b_del == 1">删除</el-button>
        <el-button icon="iconfont icon-pass" type="primary" @click="abolishFun(1)" size="mini" v-if="authority.b_pass == 1">审核</el-button>
        <el-button plain icon="iconfont icon-zuofei" @click="abolishFun(2)" class="addBtn" size="mini" v-if="authority.b_invalid == 1">作废</el-button>
        <el-button plain icon="iconfont icon-fanshenhe" @click="AUDIT_GET" size="mini" v-if="authority.b_nopass == 1">
          弃审</el-button>
        <el-button plain icon="el-icon-download" @click="downloadF" size="mini" v-if="authority.b_export_file == 1">下载模板
        </el-button>
        <el-button plain icon="iconfont icon-daoru5" @click="dialogEmployee" class="addBtn" v-if="authority.b_import == 1" size="mini">导入</el-button>
        <el-button plain size="mini" icon="el-icon-printer" v-if="authority.b_print == 1" @click="printF()">批量打印
        </el-button>
      </div>
      <div class="table_container">
        <xTable :ajax="{ url: 'otherStorageGetInStorageList', params: params }" :sign="indexSign" ref="xTable" :tableColumn="tableColumn" :xData="tableData" />
      </div>
      <upload :options="options" @receiveUploadMsg="receiveUploadMsg" ref="upload"></upload>
      <errorDialog :errorData="trunErrTable" :errorState="errorState"></errorDialog>
    </div>
    <div v-if="printingStatusGet">
      <lodopDelivery ref="lodopDelivery" :title="title" @Search="onSearch"></lodopDelivery>
    </div>
  </div>
</template>

<script>
import lodopDelivery from "common/components/public/PublicPrint.vue"; //其它入库单打印
import errorDialog from "common/components/plugin/eldialog/errorDialog.vue";
import { TableHead } from "./config/instorageIndex.js";
import { ThisScope } from "@/utils/commonCrud/CRUD.js";
import { CompanySwitchClear } from "common/utils/public_function/commonUtils";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "instorageOtheninstorageInstorageIndex",
  mixins: [TableHead],
  data() {
    return {
      options: {
        title: "导入",
        url: "/inventory/OtherStorage/importOtherStorage"
      },
      title: "",
      indexSign: {
        noDisabled: true,
        arr: []
      }, // 失败行
      errorState: false,
      trunErrTable: {},
      purchaseprice: true, //int 初始化显示数据列表展示
      planAdds: false, //增加 修改采购计划列表
      examineprice: false, //查看采购计划详情
      lock: 0,
      AccordingParams: {}, //参数传递值
      tableHeight: 600
    };
  },
  props: {
    type: {
      type: [Number, String],
      default: 0
    },
    analyzeType: {
      type: [Number, String],
      default: 0
    }
  },
  computed: {
    ...mapGetters(["printingStatusGet"])
  },
  components: {
    lodopDelivery,
    errorDialog
  },
  methods: {
    ...mapActions(["printingStatus"]),
    supplierchange(val) {
      if (val != "") {
        this.params.supplier_name = this.supplierList.filter(
          obj => obj.supplier_id === val
        )[0].supplier_name;
      }
    },
    ckChange(val) {
      if (val != "") {
        this.params.warehouse_name = this.warehouseList.filter(
          obj => obj.warehouse_id === val
        )[0].warehouse_name;
      }
    },
    businessIdChange(val) {
      if (val != "") {
        this.params.buyer_name = this.employeeList.filter(
          obj => obj.employee_id === val
        )[0].employee_name;
      }
    },
    dialogEmployee() {
      this.$refs.upload.stateOpen();
    },
    receiveUploadMsg() {
      this.$refs.xTable._getData();
    },
    printF() {
      let companyFlag = false;
      const companyIds = this.multipleSelection;
      for (var i = 1; i < companyIds.length; i++) {
        if (companyIds[i - 1].company_id != companyIds[i].company_id) {
          companyFlag = true;
          break;
        }
      }
      if (!this.multipleSelection.length) {
        this.$message({
          message: "请选择单据进行打印",
          type: "warning"
        });
      } else if (companyFlag) {
        this.$message({
          message: "请选择相同公司单据进行打印",
          type: "warning"
        });
      } else {
        let flag = true,
          errorGoodsArr = [];
        this.multipleSelection.forEach(item => {
          if (item.after_auditor_print_status == 1) {
            //审核后才可打印
            if (item.auditor_status != 2) {
              errorGoodsArr.push(item.instorage_sn);
              flag = false;
            }
          }
        });

        if (flag) {
          this.multipleSelection.forEach(item => {
            this.printSheet(item, true);
          });
        } else {
          let str = errorGoodsArr.map((item, i) => `${i + 1}.${item}`);
          this.$alert(
            `以下未审核的单据不可打印：<br/>
															            <div style="color:#e6a23c">
															              ${str.join("，")}
															            <div>`,
            "提示",
            {
              dangerouslyUseHTMLString: true
            }
          );
        }
      }
    },
    printSheet(row, index = 0) {
      this.printingStatus(true);
      this.$set(this, "title", "otherStoragePrint");
      this.$nextTick(() => {
        this.$refs.lodopDelivery.printPdf(
          {
            instorage_id: row.instorage_id
          },
          index
        );
      });
    },
    companyChange(val) {
      if (val != "") {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
      }
      CompanySwitchClear(this, this.params);
    },
    downloadF() {
      this.$axMethod.downloadFileUrlinventory().then(res => {
        if (res.data.code == 200) {
          window.open(res.data.data.url);
        }
      });
    },
    AUDIT_GET() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: `请选择需要弃审的信息！`,
          type: "warning"
        });
        return false;
      }
      let addr = [];
      var cardoctor = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].instorage_id);
        cardoctor.push({
          bill_id: this.multipleSelection[i].instorage_id,
          bill_type: this.multipleSelection[i].type === '其他入库' ? 2 : this.multipleSelection[i].type === '调拨入库' ? 4 : 3
        })
      }
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 2,
          bill_id: addr.join(","),
          operation_type: 5,
          type_info: JSON.stringify(cardoctor)
        })
        .then(() => {
          const AUDIT_GE = new ThisScope({
            _this: this,
            id: "instorage_id",
            url: "OtherunAuditorPass",
            title: "弃审",
            errId: "instorage_sn",
            params: { is_list: 1 }
          });
          AUDIT_GE.callback();
        });
    },
    // 作废
    abolishFun(type) {
      // 审核 1 作废2
      let txt = "",
        API = "";
      switch (type) {
        case 1:
          txt = "审核";
          API = "otherStorageAuditorPassInStorage";
          break;
        case 2:
          txt = "作废";
          API = "otherStorageAuditorInvalidInStorage";
          break;
        default:
          break;
      }
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: `请选择需要${txt}的信息！`,
          type: "warning"
        });
        return false;
      }
      let addr = [];
      var cardoctor = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].instorage_id);
        cardoctor.push({
          bill_id: this.multipleSelection[i].instorage_id,
          bill_type: this.multipleSelection[i].type === '其他入库' ? 2 : this.multipleSelection[i].type === '调拨入库' ? 4 : 3
        })
      }
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 2,
          bill_id: addr.join(","),
          operation_type: type === 1 ? 4 : 6,
          type_info: JSON.stringify(cardoctor)
        })
        .then(() => {
          let creditArr = addr.toString();
          let funcan = {
            eachData: {
              instorage_id: creditArr.toString()
            },
            UrlApi: API,
            list: {}
          };
          this.$common.audit(funcan, this.onSearch);
        });
    },
    departmentChange(val) {
      if (val != "") {
        this.params.department_name = this.departmentList.filter(
          obj => obj.department_id === val
        )[0].department_name;
      }
      this.$set(this.params, "buyer_id", "");
    },
    onReset() {
      this.timeValue = [];
      this.auditValue = [];
      this.$set(this, "params", {
        auditor_status: 0
      });
      this.indexSign.arr = [];
      this.onSearch();
    },
    seeF(scope, row) {
      this.$router.push({
        path: "/inventory/otheninstorage/otheninstorageSee",
        query: {
          instorage_id: row.instorage_id,
          after_auditor_print_status: row.after_auditor_print_status
        }
      });
    },
    addF() {
      this.$router.push({
        path: "/inventory/otheninstorage/otheninstorageAdd"
      });
    },
    modify(scope, row) {
      if (row && row.is_execute == 1) {
        this.$message.error("wms系统正在操作此单据，暂不支持单据修改");
        return false;
      }
      this.$router.push({
        path: "/inventory/otheninstorage/otheninstorageAdd",
        query: {
          instorage_id: row.instorage_id
        }
      });
    },
    onSearch(num) {
      if (this.timeValue) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      if (num) {
        this.indexSign.arr = [];
      }
      this.$refs.xTable._getData(num);
    },
    //删除
    deletes() {
      if (this.multipleSelection.length < 1) {
        this.$message({
          message: `请选择需要删除的信息！`,
          type: "warning"
        });
        return false;
      }
      let addr = [];
      var cardoctor = [];
      for (var i = 0; i < this.multipleSelection.length; i++) {
        addr.push(this.multipleSelection[i].instorage_id);
        cardoctor.push({
          bill_id: this.multipleSelection[i].instorage_id,
          bill_type: this.multipleSelection[i].type === '其他入库' ? 2 : this.multipleSelection[i].type === '调拨入库' ? 4 : 3
        })
      }
      // 校验对接车郎中
      this.$baseData
        .checkCarDoctor({
          bill_type: 2,
          bill_id: addr.join(","),
          operation_type: 3,
          type_info: JSON.stringify(cardoctor)
        })
        .then(() => {
          var deledata = {
            deleID: "instorage_id",
            deleSelect: this.multipleSelection,
            deleApi: "otherStorageDelInStorage",
            list: {}
          };
          this.$common.deletes(deledata, "删除", this, this.onSearch);
        });
    }
  },
  created() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$set(this, "params", {
      auditor_status: 0
    });
  },
  activated() {
    this.onSearch(window.goPage);
  }
};
</script>
